@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    padding: 2rem;
    margin: 0 auto 2rem auto;
    width: 100%;

    @include m.tablet{
        width: 80%;
    }

    

    .campo{
        margin-top: 2rem;
        display: flex;
        flex-direction: column;
        gap: 3rem;

        label {
            margin-left: 35px;

            color: v.$blanco;
            font-size: 1.4rem;

            &.error-label {
                color: v.$rojo;
            }

            &.exito-label {
                color: v.$verde;
            }
        }

        span {
            font-size: 1.5rem;
            color: v.$blanco;
            margin-left: 20px;
        }

        .input-container {
            display: flex;
            justify-content: center;
            position: relative;
            width: 100%;
        }

        .input-icon {
            position: absolute;
            top: 50%;
            left: 30px;
            @include m.tablet{
                left: 50px;
            }
            /* Ajusta la posición del icono */
            transform: translateY(-50%);
            color: #999;
            /* Color del icono */
        }

        input {
            display: block;
            width: 90%;
            padding: 1.5rem 0.5rem;
            border: none;
            border-bottom: 1px solid v.$blanco;
            background-color: transparent;
            font-size: 1.5rem;
            box-sizing: border-box;
            padding-left: 30px;
            @include m.tablet{
                padding-left: 50px;
            }
            color: v.$blanco;

            &:focus {
                outline: none;
            }

            &.error_border {
                border-bottom: 1px solid v.$rojo;
            }

            &.exito_border {
                border-bottom: 1px solid v.$verde;
            }
        }


        .submit {
            font-size: 1.5rem;
            width: 90%;
            margin: auto;
            cursor: pointer;
            padding: 1rem 0;

            &:hover{
                color: v.$negro;
                background-color: v.$blanco;
            }
        }
    }
}

